<h1 id="android-asynchronous-operations" tabindex="-1"><a class="header-anchor" href="#android-asynchronous-operations" aria-hidden="true">#</a> Android Asynchronous Operations</h1>
<p>Handling long running operations is a bit complicated in Android because the Main Application thread (which executes all your code be default) should primarily be used for updating the graphical user interface, and not for executing long running operations. Let's take a look at the workarounds we used in Java.</p>
